// Login的请求方法
import request from '@/utils/request'

// 请求发送验证码
export function sendPhoneSMS (mobile) {
  return request({
    url: 'sms/codes/' + mobile
  })
}

// 登录接口
export function userLogin (form) {
  return request({
    url: 'authorizations',
    method: 'POST',
    data: form
  })
}
